                  <v-text-field  value="Point value scale" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.input_min" label="input_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.input_max" label="input_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.scale_min" label="scale_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.scale_max" label="scale_max" type="number"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat dark>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="advancedHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="{
                  showFirstLastPage: true, 
                  firstIcon: 'mdi-chevron-double-left',
                  lastIcon: 'mdi-chevron-double-right',
                  prevIcon: 'mdi-chevron-left',
                  nextIcon: 'mdi-chevron-right',
                }"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)" v-if="!isClickSaveImport"> mdi-delete </v-icon>
                  <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item.uuid, 'uuid')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="createPoint(item)" v-if="isClickSaveImport" :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.name="{ item }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" v-model="item.name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.name" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.register="{ item }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid', item)" v-model="item.register" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.register" type="number" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.register, 'register', item) || 'Register Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.enable="{ item }">
                  <v-checkbox v-model="item.enable"></v-checkbox>
                </template>
                <template v-slot:item.function_code="{ item }">
                  <v-select v-model="item.function_code" :items="point_types" required :rules="[rules.required]" v-on:change="$refs.form.resetValidation()" ></v-select>
                </template>
                <template v-slot:item.value="{ item }">
                  <v-text-field v-model="item.value" type="number" single-line style="width: 100px" ></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid">Save</v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="350px">
        <v-card>
          <v-card-title>Mass Edit</v-card-title>
          <v-card-text>
            <v-select v-if="editField === 'data_type'" v-model="editAll[editField]" :items="point_data_types" :label="toUpperTextKey(editField)" ></v-select>
            <v-select v-if="editField === 'data_endian'" v-model="editAll[editField]" :items="point_data_endians" :label="toUpperTextKey(editField)" ></v-select>
            <v-select v-if="editField === 'value_operation'" v-model="editAll[editField]" :items="value_operation" :label="toUpperTextKey(editField)" ></v-select>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" min="0" :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
            <v-text-field v-if="typeof editAll[editField] === 'string' && editField !== 'function_code' && editField !== 'data_type' && editField !== 'data_endian' && editField !== 'value_operation'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>

    <!-- Pre Made Point Dialog -->
    <v-dialog v-model="dialogPreMade" max-width="1000px">
      <v-card>
        <v-card-title>
          <div class="headline">Create Points</div>
        </v-card-title>
        <v-card-text style="max-height:40vh; overflow: auto;">
            <v-form v-model="valid" ref="form" lazy-validation >
              <v-row>
                <v-col >
                  <template v-if="!modbusPoints.length">
                    No pre made points.
                  </template>
                  <template v-else>
                    <v-text-field v-model="searchPrePoints" append-icon="mdi-magnify" label="Search" single-line hide-details class="mb-1"></v-text-field>
                    <v-treeview v-model="selectedPoints" :items="modbusPoints" selectable return-object open-all class="text-left" @input="updatedSelected" activatable ref="tree" :search="searchPrePoints"></v-treeview>
                  </template>
                </v-col>
                <v-divider vertical></v-divider>
                <v-col cols="8" >
                  <template v-if="!selectedPoints.length">
                    No points selected.
                  </template>
                  <template v-else>
                    <div v-for="(key, index) of Object.keys(selectedPointsShow)" :key="index" class="mb-4 pa-2">
                      <h3 class="mb-2" style="opacity: 0.6;">{{getDeviceNameByDeviceId(key)}}</h3>
                      <v-row >
                        <v-col cols="12" sm="6" md="6" v-for="(point, idx) of selectedPointsShow[key]" :key="idx">
                          <v-text-field v-model="point.value.name" :label="`pre point: ${point.name}`" required :rules="[() => !isExistingvalue(point.value.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                        </v-col>
                      </v-row><br/>
                      <v-divider></v-divider>
                    </div>
                  </template>
                </v-col>
              </v-row>
            </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogPreMade = false"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="savePoints" :disabled="!valid || selectedPoints.length == 0"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-app>
</template>

<script>
import { isJsonString, copyToClipboard, exportJSON, exportCSV, exportExcel, toUpperTextKey } from '../../utils/utils';
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import MbRightSidebar from './MbRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';

export default {
  components: {
    MqttConsole,
    MbRightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog
  },
  data: () => ({
    topic: 'rubix/points/debug',
    tab: 0,
    objectKeys: Object.keys,
    objectValues: Object.values,
    editedIndex: -1,
    action: '',
    device: {},
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    pointHeaders: [
      { text: 'Name', value: 'name', width: '90px' },
      { text: 'Enable', value: 'enable', sortable: false },
      { text: 'Value', value: 'value', sortable: false },
      { text: 'Last value change timestamp', value: 'ts_value', sortable: false },
      { text: 'Writable', value: 'writable', sortable: false },
      { text: 'Fault', value: 'fault', sortable: false },
      { text: 'Write value', value: 'priority_array_write', sortable: false},
    ],
    advancedHeaders: [
      { text: 'Actions', value: 'actions', sortable: false },
      { text: 'Name', value: 'name', width: '90px' },
      { text: 'Driver', value: 'driver', sortable: false },
      { text: 'Enable', value: 'enable', sortable: false },
      { text: 'Function code', value: 'function_code', sortable: false },
      { text: 'Register', value: 'register', sortable: false },
      { text: 'Register length', value: 'register_length', sortable: false },
      { text: 'COV threshold', value: 'cov_threshold', sortable: false },
      { text: 'Data type', value: 'data_type', sortable: false },
      { text: 'Data endian', value: 'data_endian', sortable: false },
      { text: 'Value', value: 'value', sortable: false },
      { text: 'Value original', value: 'value_original', sortable: false },
      { text: 'Value round', value: 'value_round', sortable: false },
      { text: 'Value offset', value: 'value_offset', sortable: false },
      { text: 'Value operation', value: 'value_operation', sortable: false },
      { text: 'Value raw', value: 'value_raw', sortable: false },
      { text: 'Input max', value: 'input_max', sortable: false },
      { text: 'Input min', value: 'input_min', sortable: false },
      { text: 'Scale max', value: 'scale_max', sortable: false },
      { text: 'Scale min', value: 'scale_min', sortable: false },
      { text: 'Writable', value: 'writable', sortable: false },
      { text: 'Fault', value: 'fault', sortable: false },
      { text: 'Write value', value: 'priority_array_write', sortable: false },
      { text: 'Fault message', value: 'fault_message', sortable: false },
      { text: 'Fault timestamp', value: 'ts_fault', sortable: false },
      { text: 'Last value change timestamp', value: 'ts_value', sortable: false },
      { text: 'Created on', value: 'created_on', sortable: false },
      { text: 'Updated on', value: 'updated_on', sortable: false },
      { text: 'Device uuid', value: 'device_uuid', sortable: false },
      { text: 'UUID', value: 'uuid', align: 'start', sortable: false },
    ],
    point_types: [ 'READ_COILS', 'READ_DISCRETE_INPUTS', 'READ_HOLDING_REGISTERS', 'READ_INPUT_REGISTERS', 'WRITE_COIL', 'WRITE_REGISTER', 'WRITE_COILS', 'WRITE_REGISTERS', ],
    point_data_types: ['RAW', 'INT16', 'UINT16', 'INT32', 'UINT32', 'FLOAT', 'DOUBLE', 'DIGITAL'],
    point_data_endians: ['LEB_BEW', 'LEB_LEW', 'BEB_LEW', 'BEB_BEW'],
    value_operation: ['ADD', 'SUBTRACT', 'MULTIPLY', 'DIVIDE', 'BOOL_INVERT'],
    history_data_types: ['INTERVAL', 'COV'],
    currentItem: {},
    editedItem: {
      name: '',
      register: 0,
      register_length: 1,
      function_code: 'READ_HOLDING_REGISTERS',
      enable: true,
      priority_array_write:  {"_16": 0 },
      data_type: 'RAW',
      data_endian: 'BEB_LEW',
      value_round: 1,
      value_offset: 0,
      value_operation: 'ADD',
      input_min: null,
      input_max: null,
      scale_min: null,
      scale_max: null,
      cov_threshold: 0,
      history_enable: false,
      history_type: 'INTERVAL',
      history_interval: 15,
      device_uuid: '',
      value: 0,
    },
    defaultItem: {
      name: '',
      register: 0,
      register_length: 1,
      function_code: 'READ_HOLDING_REGISTERS',
      enable: true,
      priority_array_write:  { "_16": 0 },
      data_type: 'RAW',
      data_endian: 'BEB_LEW',
      value_round: 1,
      value_offset: 0,
      value_operation: 'ADD',
      input_min: null,
      input_max: null,
      scale_min: null,
      scale_max: null,
      cov_threshold: 0,
      history_enable: false,
      history_type: 'INTERVAL',
      history_interval: 15,
      device_uuid: '',
      value: 0,
    },
    rules: {
      required: value => !!value || 'Required.',
    },
    dataImport: '',
    importArray: [],
    dialogImport: false,
    e1: 1,
    dialogImportExcel: false,
    search: '',
    searchWizard: '',
    dialogMassEdit: false,
    editAll: {
      enable: false,
      register_length: 0,
      function_code: "READ_HOLDING_REGISTERS",
      data_type: 'RAW',
      data_endian: 'BEB_LEW',
      value_round: 1,
      value_offset: 0,
      value_operation: 'ADD',
      writable: false,
      cov_threshold: 0,
      history_enable: false,
      history_type: 'INTERVAL',
      history_interval: 15,
      input_min: 0,
      input_max: 0,
      scale_min: 0,
      scale_max: 0,
      value: 0,
    },
    editField: "",
    intervalId: null,
    expanded: [],
    isClickSaveImport: false,
    dirs: [],
    advancedView: false,
    modbusPoints: [],
    dialogPreMade: false,
    selectedPoints:[],
    selectedPointsShow:[],
    searchPrePoints: '',
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Point' : 'Edit Point';
    },
    refreshTime() {
      const { setting } = this.$store.state;
      return setting.refreshTime;
    },
    enableAutoRefresh() {
      const { setting } = this.$store.state;
      return setting.enableAutoRefresh;
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getData();
      }
    },
    selectedPoints(val,oldVal) {
      val.forEach(point=>{
        point.value.name = point.name;
      });
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).then(r => {
      next();
    });
  },
  mounted() {
    this.getModbusPoints();
    this.getData();
    //set auto refresh
    if(this.refreshTime &&  this.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getData();
        this.$toasted.show(`Automatically refresh every ${this.refreshTime}ms`, { type: 'info' });
      }, this.refreshTime);
    }
  },
  methods: {
    async getData() {
      this.device = await axios.get(`point-server/device/${this.$route.params.id}?with_children=true`);
      this.getDirs();
      this.editedItem.device_uuid = this.defaultItem.device_uuid = this.device.uuid;
      this.device.points = this.device.points.map(item => {
        const keys = this.objectKeys(item.point_store);
        if (keys && keys.length > 0) {
          keys.forEach(key => {
            item = { ...item, [key]: item.point_store[key] };
          });
        }
        delete item.point_store;
        delete item.point_uuid;
        return item;
      });
    },
    getDirs(){
      axios.get(`point-server/network/${this.device.network_uuid}`).then((data)  => {
        this.dirs= [{text: 'NETWORKS', link: '/modbus/networks'}];
        this.dirs.push({text: `NET: ${data.name}`, link: `/modbus/networks/${data.uuid}/devices`});
        this.dirs.push({text: `DEV: ${this.device.name}`});
      });
    },
    editItem(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    createItem() {
      this.editedIndex = -1;
      this.expanded = [];
      this.editedItem = Object.assign({}, this.defaultItem);
      this.dialog = true;
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updatePoint(this.editedItem);
        } else {
          this.createPoint(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.device.points.indexOf(this.currentItem);
      this.device.points.splice(this.editedIndex, 1);
      await axios.delete(`point-server/point/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport= false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form.validate()) {
          this.isClickSaveImport = true;
          try{
            await this.createMutiPoints(this.importArray);
            this.closeDialogImport();
            this.selected = [];
          }catch(ex){}
        }
      }
    },
    clickImportJson() {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.device_uuid = this.device.uuid;
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(point=>{
        delete point.uuid;
        delete point.value_raw;
        delete point.priority_array_write;
        return point;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName, item) {
      let points = _cloneDeep(this.device.points);
      if (this.editedIndex !== -1) {
        //when edit
        points.splice(this.editedIndex, 1);
      }
      if (fieldName === 'register') {
        value = Number(value);
        if (item) {
          points = [...points].filter(p => p.function_code === item.function_code);
        }
      }
      return points.find(p => p[fieldName] == value) != null;
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      if (type === 'csv') {
        return exportCSV('points', array);
      }
      if (type === 'excel') {
        return exportExcel('points', array);
      }
      if (type === 'json') {
        return exportJSON('points', JSON.stringify(array));
      }
    },
    updatePoint(data) {
      axios.put(`point-server/point/${data.uuid}`, data)
        .then(response => {
          const index = this.device.points.findIndex(n => n.uuid == response.uuid);
          Object.assign(this.device.points[index], response);
          this.$toasted.show('update success');
          this.close();
          this.expanded = [];
        });
    },
    async createPoint(data) {
      await axios.post(`point-server/point`, data)
        .then(response => {
          this.device.points.push(response);
          if (this.importArray && this.importArray.length > 0) {
            const index = this.importArray.findIndex(n => n.name == response.name);
            if(index > -1){
              Object.assign(this.importArray[index], response);
            }
          }
          this.$toasted.show('create success');
          this.close();
        });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor + ' set-row-height';
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      this.editAll = {
        enable: false,
        register_length: 0,
        function_code: "READ_HOLDING_REGISTERS",
        data_type: 'RAW',
        data_endian: 'BEB_LEW',
        value_round: 1,
        value_offset: 0,
        value_operation: 'ADD',
        writable: false,
        cov_threshold: 0,
        history_enable: false,
        history_type: 'INTERVAL',
        history_interval: 15,
        input_min: 0,
        input_max: 0,
        scale_min: 0,
        scale_max: 0,
        value: 0,
      }
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    expandItem({item}) {
      this.editedIndex = this.device.points.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    onClickRefresh(){
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    getModbusPoints() {
      axios.get('modbus').then(r => {
        if(!r || !r.length) return;
        const devices = r;
        this.modbusPoints = [{id: 1, name:"Select All", children: []}];
        devices.forEach(device=>{
          if(device.points && device.points.length > 0){
            const deviceChild = [];
            device.points.forEach(point=>{
              deviceChild.push({id: point._id, name: point.name, value: point} );
            });
            this.modbusPoints[0].children.push({id: device._id, name: `Device: ${device.name}`, children: deviceChild});
          }
        });
      })
    },
    async savePoints(){
      if (this.$refs.form.validate()) {
          const points = this.selectedPoints.map(p=>p.value);
          points.forEach(point=>{
            if(point.uuid) delete point.uuid;
            if(typeof point.priority_array_write !== 'object') delete point.priority_array_write;
          });
          this.dialogPreMade = false;
          await this.createMutiPoints(points);
        }
    },
    async createMutiPoints(points) {
      const promises=[];
      for(const item of points) {
        item.device_uuid = this.device.uuid; //update to the current device_uuid
        item.register_length=item.register_length? item.register_length:0;
        if(!item.uuid) {
          promises.push(this.createPoint(item));
        }
      }
      await Promise.all(promises);
    },
    updatedSelected(){
      this.selectedPointsShow = this.selectedPoints.reduce((grs, currPoint) => {
        grs[currPoint.value.device_id] ? grs[currPoint.value.device_id].push(currPoint) : grs[currPoint.value.device_id] = [currPoint];
        return grs;
      }, {});
    },
    getDeviceNameByDeviceId(device_id){
      const devices = this.modbusPoints[0].children;
      return (devices.find(d=>d.id == device_id)).name;
    },
 
  },
};
</script>
