import http from '../axios';
import _clone from 'lodash/clone';

const formatNetworkPoints = networks => {
  let networkPoints = [];
  for (let network of networks) {
    for (let device of network.devices) {
      for (let point of device.points) {
        const item = {
          id: point.uuid,
          uuid: point.uuid,
          name: `${network.name}:${device.name}:${point.name}`,
        };
        const networkO = { ...network };
        delete networkO.devices;
        const deviceO = { ...device };
        delete deviceO.points;
        const pointO = { ...point };

        item = {
          ...item,
          network: networkO,
          device: deviceO,
          point: pointO,
        };

        networkPoints.push(item);
      }
    }
  }
  return networkPoints;
};

const formatNetworkDevicePoints = networks => {
  let genericDevices = [];
  let genericNetworks = _clone(networks);
  for (let network of networks) {
    for (let device of network.devices) {
      const item = {
        id: device.uuid,
        uuid: device.uuid,
        name: `${network.name}:${device.name}`,
      };
      const networkO = { ...network };
      delete networkO.devices;
      const deviceO = { ...device };

      item = {
        ...item,
        network: networkO,
        device: deviceO,
      };

      genericDevices.push(item);
    }
  }
  return { genericNetworks, genericDevices };
};

const formatLoraPoints = gatewayDevices => {
  let loraPoints = [];
  let loraDevices = [];
  for (let gatewayDevice of gatewayDevices) {
    loraDevices.push({ ...gatewayDevice });
    for (let loraDevice of gatewayDevice.points) {
      loraPoints.push({
        item: loraDevice,
        deviceName: gatewayDevice.name,
        pointName: loraDevice.name,
        name: `${gatewayDevice.name}:${loraDevice.name}`,
        id: loraDevice.uuid,
      });
    }
  }
  return { loraPoints, loraDevices };
};

export const fetchRubixPSGenericPoints = () =>
  http.get('/point-server/generic/network?with_children=true&points=true').then(formatNetworkPoints);

export const fetchGenericDevices = () =>
  http.get('/point-server/generic/network?with_children=true&points=true').then(formatNetworkDevicePoints);

export const fetchBacnetPoints = () => http.get('/bac-net/point');

export const fetchLoraPoints = () => http.get('/lora/device').then(formatLoraPoints);

export const fetchModbusPoints = () =>
  http.get('/point-server/network?with_children=true&points=true').then(formatNetworkPoints);
