<template>
  <div>
    <v-card>
      <v-card-title class="headline">
        Auto refresh
      </v-card-title>
      <v-card-text>
        <v-row>
          <v-col cols="12" sm="12" md="12" class="d-flex">
            <div style="margin: auto 0;">Refresh Time:</div>&nbsp;&nbsp;&nbsp;
            <v-radio-group v-model="setting.refreshTime" row >
              <v-radio label="5 sec" value="5000" ></v-radio>
              <v-radio label="15 sec"  value="15000" ></v-radio>
              <v-radio label="30 sec" value="30000" ></v-radio>
              <v-radio label="1 min"  value="60000" ></v-radio>
            </v-radio-group>
          </v-col>
          <v-col cols="12" sm="12" md="12">
            <v-switch v-model="setting.enableAutoRefresh" label="Enable Auto Refresh"></v-switch>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="onSave()" :disabled="!setting.enableAutoRefresh && !setting.refreshTime"> Save </v-btn>
      </v-card-actions>
    </v-card>
    <v-card>
      <v-card-title class="headline">
        Timezone
      </v-card-title>
      <v-card-text>
        <v-autocomplete @change="onSave" v-model="setting.timezone" :items="timezones" :label="'Timezones'" persistent-hint></v-autocomplete>
      </v-card-text>
    </v-card>
    <v-card>
      <v-card-title class="headline">
        Tags
      </v-card-title>
      <v-card-text class="text-left">
        <v-btn x-small color="success" @click="showTagDialog" >
          Create &nbsp;<v-icon small>mdi-plus</v-icon>
        </v-btn>
        <v-btn x-small color="primary" @click="showImportDialog" >
          Import&nbsp;<v-icon small>mdi-import</v-icon>
        </v-btn>
        <v-divider inset vertical></v-divider>
        <v-btn x-small @click="clickExport" color="#009688" class="white--text">
          Export&nbsp;<v-icon small> mdi-export </v-icon>
        </v-btn>
        <v-data-table :headers="headers" :items="tags">
          <template v-slot:item.data="{item}">{{JSON.stringify(item.data)}}</template>
          <template v-slot:item.tools="{item, index}">
            <v-btn @click="() => toggleConfirmDialog(index)" icon small color="error" class="white--text"><v-icon small dark >mdi-delete</v-icon></v-btn>
            <v-btn @click="() => toggleTagDialog(index, item)" icon small color="success" class="white--text"><v-icon small dark >mdi-rename-box</v-icon></v-btn>
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>

    <v-dialog v-model="tagDialog" max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">Tags</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-text-field required :rules="[rules.required]" v-model="tagData.name" label="Name" required></v-text-field>
              <div class="text-left">
                <code>{"key": "value", "key1": "value1"}</code>
              </div>
              <v-text-field required :rules="[rules.required]" v-model="tagData.data" label="Data" required></v-text-field>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeTagDialog">Cancel</v-btn>
          <v-btn :disabled="!valid" color="blue darken-1" @click="saveTag" text>Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogImport" max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">Import</span>
        </v-card-title>
        <v-card-text>
          Exp: <code>[{"name": "name 1", "data": {"key": "value", "key1": "value1"}}]</code>
          <v-container>
            <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="showImportDialog">Cancel</v-btn>
          <v-btn color="blue darken-1" @click="saveImport" text>Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="confirmDialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">Confirm? </v-card-title>
        <v-card-text>Do you want to delete?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="closeDialog" > No </v-btn>
          <v-btn color="red darken-1" text @click="onConfirm"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import axios from "../../axios";
  import TextFieldDisplay from '../Common/TextFieldDisplay';
  import MqttConsole from '../Mqtt/MqttConsole';
  import { copyToClipboard } from '../../utils/utils';

  export default {
    components: {
      TextFieldDisplay, MqttConsole
    },
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      timezones: [],
      tagData: {},
      objectKeys: Object.keys,
      dialogImport: false,
      dataImport:"",
      selectedIndex: -1,
      confirmDialog: false,
      tagDialog: false,
      valid: false,
      headers: [
        {
          text: 'Name',
          align: 'start',
          value: 'name',
        },
        { text: 'Data', value: 'data' },
        { text: 'Tools', value: 'tools' },
      ],
    }),
    computed: {
      setting(){
        const result = this.$store.state.setting;
        if (!result.timezone) {
          result.timezone = 'BaseOnBrowser';
        }
        return result;
      },
      tags() {
        const {tags} = this.$store.state.setting;
        return tags;
      }
    },
    mounted() {
      this.getTimezones();
    },
    methods: {
      async saveTag() {
        const isValid = this.$refs.form.validate();
        if (!isValid) {
          return;
        }
        const data = {...this.tagData, data: JSON.parse(this.tagData.data)};
        const tags = this.tags || [];
        if (this.selectedIndex === -1) {
          tags.push(data);
        } else {
          tags[this.selectedIndex] = {...data};
        }
        this.setting.tags = [...tags];
        await axios.put(`setting`, this.setting);
        this.$store.commit('changeSetting', this.setting);
        this.tagDialog = false;
        this.$toasted.show('success');
        this.selectedIndex = -1;
      },
      async onConfirm() {
        this.tags.splice(this.selectedIndex, 1);
        await axios.put(`setting`, this.setting);
        this.confirmDialog = false;
        this.selectedIndex = -1;
      },
      closeDialog() {
        this.confirmDialog = false;
        this.selectedIndex = -1;
      },
      closeTagDialog() {
        this.tagDialog = false;
        this.selectedIndex = -1;
      },
      toggleConfirmDialog(index){
        this.selectedIndex = index;
        this.confirmDialog = true;
      },
      toggleTagDialog(index){
        this.selectedIndex = index;
        this.tagDialog = true;
        this.tagData = {...this.tags[index], data: JSON.stringify(this.tags[index].data)};
      },
      clickExport() {
        const {tags} = this.$store.state.setting;
        const status = copyToClipboard(JSON.stringify(tags));
        const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
        const type = status ? `success` : `error`;
        return this.$toasted.show(msg, {type: type});
      },
      showImportDialog() {
        this.dialogImport = !this.dialogImport;
      },
      showTagDialog() {
        this.tagData = {};
        this.tagDialog = true;
      },
      async saveImport() {
        let setting = this.$store.state.setting;
        let tags = setting.tags;
        if (!tags) {
          tags = [];
        }
        const importData = JSON.parse(this.dataImport);
        tags = [...tags, ...importData];
        setting = {...setting, tags };
        await axios.put(`setting`, setting);
        this.$store.commit('changeSetting', setting);
        this.dialogImport = false;
        this.dataImport = '';
        this.$toasted.show('success');
      },
      getTimezones() {
        axios.get(`timezones`).then(result => {
          this.timezones = result;
          this.timezones.unshift('BaseOnBrowser')
        });
      },
      onSave() {
        axios.put(`setting`, this.setting).then(result => {
          if (result === 'successful') {
            this.$store.commit('changeSetting', this.setting);
            this.$toasted.show('Success!')
          }
        });
      }
    },
  };
</script>
<style>
  .p-relative{
    position: relative;
  }
</style>
