<template>
  <v-form v-model="valid" ref="mappingForm" :class="!isEditForm ? 'mb-4' : ''">
    <v-row dense>
      <v-col :cols="12">
        <v-autocomplete
          clearable
          v-if="isEditForm"
          return-object
          item-value="id"
          item-text="name"
          auto-select-first
          :rules="fieldValidations"
          :label="parentFieldTitle"
          v-model="selectedLoraPoint"
          :items="updatedParentPointsList"
        ></v-autocomplete>
        <v-autocomplete
          clearable
          return-object
          v-if="!isEditForm"
          item-value="id"
          item-text="name"
          auto-select-first
          :rules="[arrayRequiredValidation]"
          :label="loraSelectionLabel"
          v-model="selectedLoraPoints"
          :items="updatedParentPointsList"
          @click:clear="onLoraPointsSelectionClear"
          multiple
        >
          <template v-slot:item="{ item, attrs }">
            <template v-if="item.isHeader">
              <v-list-item ripple @click="onDeviceSelectAllClick(item.name)">
                <v-icon class="mr-2">
                  {{ selectedLoraDevices.indexOf(item.name) > -1 ? 'mdi-close-box' : 'mdi-checkbox-blank-outline' }}
                </v-icon>
                <v-list-item-title>
                  <span class="caption">Select all lora points of</span>
                  <strong>{{ item.name + ' ' }}</strong>
                  <span class="caption">device</span>
                </v-list-item-title>
              </v-list-item>
            </template>
            <template v-else>
              <v-list-item-content class="ml-5">
                <v-list-item-title>
                  <v-icon class="mr-2" v-if="!item.disabled">
                    {{ attrs['aria-selected'] == 'true' ? 'mdi-checkbox-marked' : 'mdi-checkbox-blank-outline' }}
                  </v-icon>
                  <span class="caption">{{ item.deviceName }}</span>
                  <strong>{{ item.pointName }}</strong>
                  <span v-if="item.disabled" class="already-mapped-label"> Already Mapped </span>
                </v-list-item-title>
              </v-list-item-content>
            </template>
          </template>

          <template v-slot:prepend-item>
            <v-list-item ripple @click="onLoraPointsSelectToggle">
              <v-icon class="mr-2">
                {{ isMultipleLoraPointsSelected ? 'mdi-close-box' : 'mdi-checkbox-blank-outline' }}
              </v-icon>
              <v-list-item-content>
                <v-list-item-title> Select all </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-divider class="mt-2"></v-divider>
          </template>
        </v-autocomplete>
      </v-col>
      <v-col :cols="12">
        <v-autocomplete
          clearable
          return-object
          item-value="id"
          item-text="name"
          auto-select-first
          :label="child1FieldTitle"
          @change="checkValidation"
          :rules="genericChildValidation"
          v-model="selectedChild1Point"
          :items="updatedChild1PointsList"
          :disabled="!!selectedChild2Point || isMultipleLoraPointsSelected"
        ></v-autocomplete>
      </v-col>
      <v-col :cols="12">
        <v-row dense>
          <div v-if="!isMapExistingGenericPoint">
            <v-row dense>
              <v-col cols="12">
                <v-autocomplete
                  clearable
                  return-object
                  item-value="uuid"
                  :disabled="!(selectedLoraPoints && selectedLoraPoints.length > 0)"
                  item-text="name"
                  auto-select-first
                  :rules="genericChildValidation"
                  label="Select Existing Generic Network"
                  v-model="selectedGenericNetwork"
                  :items="genericNetworks"
                ></v-autocomplete>
              </v-col>
            </v-row>
            <v-row dense>
              <v-col cols="6">
                <v-switch
                  @change="onAutoCreateToggle"
                  v-model="autoCreateDevicesAndPoints"
                  inset
                  class="pr-4"
                  label="Auto create new device(s), point(s) and mapping(s)"
                  color="primary"
                ></v-switch>
              </v-col>
              <v-col cols="6">
                <v-autocomplete
                  clearable
                  v-if="!autoCreateDevicesAndPoints"
                  return-object
                  item-value="uuid"
                  :disabled="!(selectedLoraPoints && selectedLoraPoints.length > 0)"
                  item-text="name"
                  auto-select-first
                  :rules="genericChildValidation"
                  label="Select Existing Generic Device"
                  v-model="selectedGenericDevice"
                  @change="onGenericDevicesChange"
                  :items="genericDevices"
                ></v-autocomplete>
              </v-col>
            </v-row>
          </div>
          <v-autocomplete
            clearable
            v-if="isMapExistingGenericPoint"
            return-object
            item-value="id"
            item-text="name"
            auto-select-first
            @change="checkValidation"
            :label="child2FieldTitle"
            v-model="selectedChild2Point"
            :rules="genericChildValidation"
            :disabled="!!selectedChild1Point"
            :items="updatedChild2PointsList"
          ></v-autocomplete>
        </v-row>
      </v-col>
      <!-- Generated generic points start -->
      <v-col cols="12" v-if="!isMapExistingGenericPoint && generatedGenericPoints.length > 0">
        <v-item-group multiple class="text-left">
          <v-subheader>Generated Generic Points</v-subheader>
          <v-item v-for="item in generatedGenericPoints" :key="item.name" class="ml-2 mb-2">
            <v-chip active-class="green--text" :value="item">
              {{ item.name }}
            </v-chip>
          </v-item>
        </v-item-group>
      </v-col>
      <!-- Generated generic points end -->
      <v-col cols="12">
        <v-alert v-for="error in mappingErrors" :key="error.index" dense outlined type="warning">
          Mapping Error: <strong class="white--text">{{ error.lora_point_name }}</strong> --
          <strong class="white--text">{{ error.child_point_name }}</strong> |
          <span>{{ error.message }}</span>
        </v-alert>
      </v-col>
      <v-col v-if="!isEditForm" cols="2" class="flex justify-center align-center">
        <rbx-button
          :loading="isCreatingMapping"
          :block="true"
          color="pink"
          :click="addMapping"
          title="Add"
        ></rbx-button>
      </v-col>
    </v-row>
    <v-card-actions v-if="isEditForm">
      <v-spacer></v-spacer>
      <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" :loading="isCreatingMapping" text @click="addMapping"> Save </v-btn>
    </v-card-actions>
  </v-form>
</template>
<script>
import _groupby from 'lodash/groupBy';
import _isEqual from 'lodash/isEqual';
import RbxButton from '../Common/RbxButton';
import validation from '../../common/validation.js';

export default {
  components: { RbxButton },
  props: [
    'serviceMappings',
    'isEditForm',
    'parentPoints',
    'child1Points',
    'child2Points',
    'serviceMapping',
    'parentMappingKey',
    'child1MappingKey',
    'child2MappingKey',
    'createServiceMapping',
    'createLoraGenericPointMapping',
    'parentFieldTitle',
    'child1FieldTitle',
    'child2FieldTitle',
    'onCancel',
    'genericNetworkDevices',
    'loraDevices',
    'genericNetworks',
  ],
  data() {
    return {
      uuid: null,
      valid: false,
      mappingErrors: [],
      loraDeviceNames: [],
      loraDeviceStates: {},
      autoCreateDevicesAndPoints: false,
      selectedLoraDevices: [],
      addNewNetworkToggle: false,
      addNewDeviceToggle: false,
      isMapExistingGenericPoint: false,
      isCreatingMapping: false,
      selectedLoraPoint: null,
      selectedLoraPoints: [],
      selectedChild1Point: null,
      selectedChild2Point: null,
      selectedGenericNetwork: null,
      selectedGenericDevice: null,
      genericPointValidation: [validation.required],
      genericChildValidation: [validation.required],
      fieldValidations: [validation.required],
      generatedGenericPoints: [],
      arrayRequiredValidation: value => {
        return !!(Array.isArray(value) && value.length > 0) || 'Required';
      },
    };
  },
  watch: {
    selectedLoraPoints: function (newVal, oldVal) {
      this.onGenericDevicesChange();
      if (this.isMultipleLoraPointsSelected) {
        // Make bacnet point null as multiple mapping not supported yet for bacnet
        this.selectedChild1Point = null;

        this.isMapExistingGenericPoint = false;
      }
    },
  },
  watch: {
    loraDeviceNames: function (newVal, oldVal) {
      if (!_isEqual(newVal, oldVal)) {
        this.loraDeviceStates = this.loraDeviceNames.reduce((acc, curr) => {
          acc[curr] = {
            name: curr,
            selected: false,
          };
          return acc;
        }, {});
      }
    },
  },
  mounted() {
    if (this.isEditForm && this.serviceMapping) {
      this.uuid = this.serviceMapping.uuid;
      this.isMapExistingGenericPoint = true;
      this.selectedLoraPoint = this.serviceMapping[this.parentMappingKey];
      if (this.serviceMapping.hasOwnProperty(this.child1MappingKey)) {
        this.selectedChild1Point = this.serviceMapping[this.child1MappingKey];
      }
      if (this.serviceMapping.hasOwnProperty(this.child2MappingKey)) {
        this.selectedChild2Point = this.serviceMapping[this.child2MappingKey];
      }
      this.genericChildValidation = [];
    }
  },
  computed: {
    mappings() {
      return this.serviceMappings;
    },
    selectedParentMappings() {
      return this.serviceMappings.map(serviceMapping => serviceMapping[this.parentMappingKey]);
    },
    selectedChild2Mappings() {
      return this.serviceMappings.map(serviceMapping => serviceMapping[this.child2MappingKey]);
    },
    selectedChild1Mappings() {
      return this.serviceMappings.map(serviceMapping => serviceMapping[this.child1MappingKey]);
    },
    updatedParentPointsList() {
      this.selectedLoraDevices;
      return this.updateParentListItemProperty(this.parentPoints, this.selectedParentMappings);
    },
    updatedChild2PointsList() {
      return this.updateLisItemProperty(this.child2Points, this.selectedChild2Mappings);
    },
    updatedChild1PointsList() {
      return this.updateLisItemProperty(this.child1Points, this.selectedChild1Mappings);
    },
    isAllLoraPointsSelected() {
      return this.selectedLoraPoints.length === this.updatedParentPointsList.length;
    },
    isMultipleLoraPointsSelected() {
      return this.selectedLoraPoints && this.selectedLoraPoints.length > 1;
    },
    deviceSelectAllOptions() {
      return this.loraDevices.map(loraDevice => ({
        name: loraDevice.name,
        value: loraDevice.name,
        uuid: loraDevice.uuid,
        item: loraDevice,
        selected: false,
      }));
    },
    genericDevices() {
      return (this.selectedGenericNetwork && this.selectedGenericNetwork.devices) || [];
    },
    loraSelectionLabel() {
      if (this.selectedLoraPoints.length > 0) {
        return `${this.parentFieldTitle} | ${this.selectedLoraPoints.length} LoRa points selected`;
      }
      return this.parentFieldTitle;
    },
  },
  methods: {
    addMapping() {
      const isValid = this.$refs.mappingForm.validate();
      if (!isValid) {
        return;
      }
      this.mappingErrors = [];
      if (!this.isEditForm && !this.isMapExistingGenericPoint && this.generatedGenericPoints.length > 0) {
        this.isCreatingMapping = true;
        const serviceMappings = this.generatedGenericPoints.map(serviceMapping => ({
          generic_point: {
            network: serviceMapping.network,
            device: serviceMapping.device,
            point: serviceMapping.point,
          },
          lora_point: serviceMapping.loraPoint.item,
        }));
        return this.createLoraGenericPointMapping(serviceMappings, this.autoCreateDevicesAndPoints)
          .then(data => {
            const hasError = data.every(obj => !obj.success);
            if (!hasError) {
              this.selectedLoraPoints = null;
              this.selectedLoraPoint = null;
              this.selectedChild2Point = null;
              this.selectedChild1Point = null;
              this.autoCreateDevicesAndPoints = false;
              this.selectedLoraDevices = [];
              this.$refs.mappingForm.reset();
            } else {
              this.extractDisplayError(data);
            }
          })
          .catch(err => {})
          .finally(() => {
            this.isCreatingMapping = false;
          });
      }
      if (!this.isEditForm && this.selectedLoraPoints.length == 1) {
        this.selectedLoraPoint = this.selectedLoraPoints[0].item;
      }
      this.isCreatingMapping = true;
      this.createServiceMapping(
        {
          [this.parentMappingKey]: this.selectedLoraPoint,
          [this.child1MappingKey]: this.selectedChild1Point,
          [this.child2MappingKey]: this.selectedChild2Point,
        },
        this.uuid,
      )
        .then(() => {
          if (this.isEditForm) {
            return this.onCancel();
          }
          this.selectedLoraPoints = null;
          this.selectedLoraPoint = null;
          this.selectedChild2Point = null;
          this.selectedChild1Point = null;
          this.$refs.mappingForm.reset();
        })
        .finally(() => {
          this.isCreatingMapping = false;
        });
    },
    extractDisplayError(data) {
      const errors = data
        .filter(data => !data.success)
        .map(item => {
          return {
            lora_point_name: item.data && item.data.lora_point_name,
            child_point_name: item.data && (item.data.generic_point_name || item.data.bacnet_point_name),
            message: item.message,
            index: item.rowIndex,
          };
        });
      this.mappingErrors = errors;
    },
    updateParentListItemProperty(list, selectedItems) {
      if (!selectedItems) {
        return list;
      }
      const selectedListItems = selectedItems.filter(item => item).map(item => item.id);

      const devices = list.reduce((acc, curr) => {
        if (selectedListItems.includes(curr.id)) {
          curr.disabled = true;
        } else {
          curr.disabled = false;
        }

        curr.group = curr.deviceName;
        acc.push(curr);
        return acc;
      }, []);

      const groupedDevices = _groupby(devices, value => value.group);
      const newDeviceList = Object.keys(groupedDevices).reduce((acc, groupName) => {
        let header = { text: groupName, name: groupName, isHeader: true, isNonItem: true };
        acc.push(header);
        acc.push(...groupedDevices[groupName]);
        acc.push({ divider: true, isNonItem: true });
        return acc;
      }, []);
      this.loraDeviceNames = Object.keys(groupedDevices);
      return newDeviceList;
    },
    updateLisItemProperty(list, selectedItems) {
      if (!selectedItems) {
        return list;
      }
      const selectedListItems = selectedItems.filter(item => item).map(item => item.id);

      return list.reduce((acc, curr) => {
        if (selectedListItems.includes(curr.id)) {
          curr.disabled = true;
        } else {
          curr.disabled = false;
        }
        acc.push(curr);
        return acc;
      }, []);
    },
    checkValidation() {
      if (
        this.selectedChild1Point ||
        this.selectedChild2Point ||
        this.selectedGenericDevice ||
        this.autoCreateDevicesAndPoints
      ) {
        this.genericChildValidation.pop();
      }
      if (
        !this.selectedChild1Point &&
        !this.selectedChild2Point &&
        !this.selectedGenericDevice &&
        !this.autoCreateDevicesAndPoints &&
        this.genericChildValidation.length < 1
      ) {
        this.genericChildValidation.push(validation.required);
      }
      this.$refs.mappingForm.resetValidation();
    },
    onLoraPointsSelectToggle() {
      this.$nextTick(() => {
        if (this.isMultipleLoraPointsSelected) {
          this.selectedLoraPoints = [];
        } else {
          this.selectedLoraPoints = this.updatedParentPointsList
            .slice()
            .filter(item => !item.disabled && !item.isHeader && !item.isNonItem);
        }
      });
    },
    onGenericDevicesChange() {
      if (Array.isArray(this.selectedLoraPoints) && this.selectedLoraPoints.length > 0 && this.selectedGenericDevice) {
        this.generatedGenericPoints = this.selectedLoraPoints.reduce((acc, loraPoint) => {
          const generatedLoraPoint = {};
          generatedLoraPoint.network = this.selectedGenericNetwork;
          generatedLoraPoint.device = this.selectedGenericDevice;
          generatedLoraPoint.point = loraPoint.item;
          generatedLoraPoint.name = `${generatedLoraPoint.network.name}:${generatedLoraPoint.device.name}:${generatedLoraPoint.point.name}`;
          generatedLoraPoint.loraPoint = loraPoint;
          acc.push(generatedLoraPoint);
          return acc;
        }, []);
      } else {
        this.generatedGenericPoints = [];
      }
      this.checkValidation();
    },
    onMapExistingChange() {
      this.selectedChild2Point = null;
      if (this.genericChildValidation.length < 1) {
        this.genericChildValidation.push(validation.required);
      }
      this.generatedGenericPoints = [];
    },
    onDeviceSelectAllClick(deviceName) {
      this.$nextTick(() => {
        this.loraDeviceStates[deviceName].selected = !this.loraDeviceStates[deviceName].selected;

        if (this.loraDeviceStates[deviceName].selected) {
          this.selectedLoraDevices.push(deviceName);
        } else {
          this.selectedLoraDevices = this.selectedLoraDevices.filter(dName => dName !== deviceName);
        }

        this.selectedLoraPoints = this.updatedParentPointsList.reduce((acc, loraPoint) => {
          if (this.selectedLoraDevices.indexOf(loraPoint.deviceName) > -1 && !loraPoint.disabled) {
            acc.push(loraPoint);
            return acc;
          }
          return acc;
        }, []);
      });
    },
    getGenericDevice(networkUUID, deviceName) {
      return {
        driver: 'GENERIC',
        enable: false,
        fault: null,
        history_enable: false,
        name: deviceName,
        network_uuid: networkUUID,
      };
    },
    onAutoCreateToggle() {
      if (this.autoCreateDevicesAndPoints) {
        this.selectedGenericDevice = null;
        this.generatedGenericPoints = this.selectedLoraPoints.reduce((acc, loraPoint) => {
          const generatedLoraPoint = {};
          generatedLoraPoint.network = this.selectedGenericNetwork;
          generatedLoraPoint.device = this.getGenericDevice(generatedLoraPoint.network.uuid, loraPoint.deviceName);
          generatedLoraPoint.point = loraPoint.item;
          generatedLoraPoint.name = `${generatedLoraPoint.network.name}:${generatedLoraPoint.device.name}:${generatedLoraPoint.point.name}`;
          generatedLoraPoint.loraPoint = loraPoint;
          acc.push(generatedLoraPoint);
          return acc;
        }, []);
      } else {
        this.generatedGenericPoints = [];
      }
      this.checkValidation();
    },
    onLoraPointsSelectionClear() {
      this.selectedLoraDevices = [];
    },
  },
};
</script>
<style scoped>
.flex {
  display: flex;
}
.already-mapped-label {
  background: green;
  color: #fff;
  padding: 3px 5px;
  border: 20%;
  border-radius: 5px;
  font-size: 0.75rem;
}
</style>
